const renderItem = (list) => {
  return list.map((item) => (
    <div key={item.id} className="col-md-2 col-md-3 col-sm-6">
      <div className="p-category">
        <a href={item.link} title="" target="_blank" rel="noreferrer">
          <div>
            <img
              src={`${item.img.url}`}
              className="img-responsive service_img"
              alt=""
            />
            <img
              src={`${item.img.url}`}
              className="img-responsive service_img_hover"
              alt=""
            />
          </div>
          <h4>{item.title}</h4>
          <p></p>
        </a>
      </div>
    </div>
  ));
};

